// 展示组件页面
<template>
  <div id="index">
    <header1 class="topbar"></header1>
    <section class="container">
      <div class="left_side">
        <mainLeft></mainLeft>
      </div>
      <div class="main_wrapper">
        <navigator class="nav"></navigator>
        <router-view></router-view>
      </div>
    </section>
  </div>
</template>

<script>
import header from '@/components/common/header'
import mainLeft from '@/components/common/mainLeft'
import navigator from '@/components/common/navigator'
import {jwtDecode} from 'jwt-decode'

export default {
  components:{
    header1: header,
    mainLeft: mainLeft,
    navigator:navigator
  },
  data() {
    return {
      username: '管理员',
    }
  },
  created(){
    this.init()
  },
  methods: {
    init(){

      let ws=new WebSocket(this.$websocketUrl,this.$jwtDecode(sessionStorage.getItem("token")).id)
              //接收数据
              ws.onmessage=(msg)=>{
                let data=JSON.parse(msg.data)
                if(data.message==='is_lock')
                  {
                    // this.$message({
                    //   message: '有人在登录',
                    //   type: 'success'
                    // })

                    this.open()

                  }
              }
    },
    open() {
        this.$confirm('系统检测到有人在登录你的账号, 是否退出?', '提示', {
          confirmButtonText: '退出',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          console.log("aaaa")
          this.exit()
          console.log("bbbb")
        }).catch(() => {
          this.$message({
            type: 'success',
            message: '已取消'
          });
        });
      },
       exit() {  //退出登录


      this.$axios({
        url: '/${process.env.peopleApi}/logout/'+JSON.parse(jwtDecode(sessionStorage.getItem("token"))).id,
        method: 'put'
      }).then(res => {
        console.log(res)
        if(res.data.code ==200) {
          
          this.$router.push({path:"/"}) //跳转到登录页面

          sessionStorage.removeItem("token")
          sessionStorage.removeItem("image")
          sessionStorage.removeItem("icon")
          sessionStorage.removeItem("activeInfo")
          sessionStorage.removeItem("index")
          sessionStorage.removeItem("now")
          this.$message({
            message: '退出成功',
            type: 'success'
          })
        }
      })

    },
  }
}
</script>

<style lang="less" scoped>
#index .nav {
  box-shadow: 1px 0 5px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
}
.container {
  display: flex;
  background-color: #fff;
}
.main_wrapper {
  overflow: hidden;
  flex: 1;
  background-color: #fff;
}
</style>




